}

@media screen and (max-width: 998px) {
    .header__layer {
        padding-top: 25px;
        .f-container {
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    .preview__layer-- {
        margin-top: 235px;
    }

    .preview {
        height: auto;
        min-height: unset;
    }
    .preview__layer {
        margin-top: 235px;

        &:before, &:after {
            content: none;
        }
    }

    .f-container .buy_ca__text {
        .buy_ca__paragraph {
            margin-left: 0;
        }
    }

    .heder__left {
        margin-bottom: 25px;
    }

    .preview__slide-img_left {
        margin-right: 15px;
    }

    .header__right_top {
        border-bottom: none;
    }

    .preview__slider .slick-dots {
        bottom: 10px;
    }

    .preview__slide-img_left, .preview__slide-img_right {
        width: 100%;
        max-width: 210px;
    }

    .pro__text:not(:nth-last-child(3)) {
        margin-bottom: 60px;
    }

    .pro__text:nth-child(odd) img {
        right: 0;
        top: 0;
        display: none;
    }

    .pro__text:nth-child(even) img {
        left: 0;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        opacity: 0.5;
    }

    .pro__text:nth-child(even) {
        margin-left: 0;
    }

    .pro__text:nth-child(odd) {
        margin-right: 0;
    }

    .heder__left {
        margin-right: 0;
    }

