        margin-top: 214px;
    }

    .card_s__price {
        margin-right: 25px;
    }

    .footer__layer {
        .footer__left, .footer__right, .footer__center {
        flex-basis: 100%;
        text-align: center;
        margin-bottom: 35px;
            }
        }

        .footer__layer .f-container>div:not(:last-child) {
            margin-right: 0;
        }

    .footer__btn {
        max-width: 320px;
    }

    .footer-contacts {
        display: flex;
        justify-content: center;
    }

    .footer-contact:not(:last-child) {
        margin-bottom: 0;
        margin-right: 15px;
    }


    .footer__social-list {
        justify-content: center
    }

    .footer__address-text {
        text-align: left
    }

    .footer__phones-num {
        white-space: nowrap
    }

    .footer__list {
        justify-content: center;
    }

    .footer__list-item:nth-of-type(2n-1) {
        margin-right: 0;
        width: auto;
    }

    .footer__list-item:not(:last-child) {
        margin-right: 10px;
    }

    .card_s__btns {
        flex-wrap: wrap
    }

    .card_s__info {
        flex-wrap: wrap
    }

    .card_s__price {
        // margin-right: 0;
        margin-bottom: 15px;
    }

    .card_s__btns .card_s__btn {
        flex-basis: 100%;
    
    }

    .card_s__btns .card_s__btn:not(:last-child) {
            margin-right: 0;
    }
}

